import React, { useEffect } from "react";
import * as d3 from "d3";
import { dataset } from "./labor_productivity";

import "./LaborProductivity.css";

export default function LaborProductivity() {
  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 600,
      height = 400;

    let tooltip = d3
      .select(".vietnam_education #chart")
      .append("div")
      .attr("class", "tooltip")
      .style("font-size", ".675rem")
      .style("color", "white")
      .style("opacity", "0")
      .style("position", "absolute")
      .style("padding", ".5em .8em")
      .style("background", "#051C2C");

    let svg = d3
      .select(".labor_productivity #chart")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.left,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    const xScale = d3
      .scaleBand()
      .domain(dataset.map((d) => d.Label))
      .range([0, width]);

    const xScaleYear = d3
      .scaleBand()
      .domain(dataset.map((d) => d.Year))
      .range([0, width]);

    const yScale = d3.scaleLinear().domain([-15, 20]).range([height, 0]);

    let xAxis = d3
      .axisBottom(xScaleYear)
      .tickFormat((d) => {
        return parseInt(d);
      })
      .tickPadding(10)
      .tickSize(10)
      .tickValues([1950, 1960, 1970, 1980, 1990, 2000, 2010, 2020]);
    let yAxis = d3.axisLeft(yScale).tickSize(-width);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(0, ${height})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(0, 0)`);

    let yAxisRightGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup right")
      .attr("transform", `translate(${width + margin.left}, 0)`);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);
    yAxis(yAxisRightGroup);

    chartGroup
      .selectAll("rect")
      .data(dataset)
      .enter()
      .append("rect")
      .attr("width", xScale.bandwidth())
      .attr("height", (d, i) => Math.abs(yScale(d.Value) - yScale(0)))
      .attr("x", (d, i) => xScale(d.Label))
      .attr("y", (d, i) => (d.Value <= 0 ? yScale(0) : yScale(d.Value)));

    const productivity = dataset.reduce((a, b) => {
      a[b.Year] = [...(a[b.Year] || []), b];
      return a;
    }, {});

    // console.log(d3.mean(productivity[1950], (x) => x.Value));

    let mean = [];

    for (let i = 0; i < Object.keys(productivity).length; i++) {
      let Value = d3.mean(productivity[1947 + i], (d) => d.Value);
      mean.push({
        Year: 1947 + i,
        Value,
      });
    }

    let line = d3
      .line()
      .x((d, i) => xScaleYear(d.Year))
      .y((d) => yScale(d.Value));
    // .curve(d3.curveMonotoneX);

    let meanGroup = chartGroup
      .append("g")
      .attr("class", "meanGroup")
      .attr("transform", `translate(${0}, 0)`);

    meanGroup
      .append("path")
      .datum(mean)
      .attr("fill", "none")
      .attr("stroke", "#000")
      .attr("stroke-width", "2")
      .attr("d", line);

    meanGroup
      .selectAll("circle")
      .data(mean)
      .enter()
      .append("circle")
      .attr("opacity", 0)
      .attr("cx", (d) => {
        return xScaleYear(d.Year);
      })
      .attr("cy", (d) => {
        return yScale(d.Value);
      })
      .attr("r", "3")
      .on("mouseover", function (event, d) {
        d3.select(this).style("opacity", 1);

        tooltip.transition().duration(1000).style("opacity", 1);

        tooltip
          .html(
            `<span>Year: ${d.Year}</span>
        <span>Avg.: ${parseFloat(d.Value.toFixed(2))}</span>
        `
          )
          .style("top", event.pageY + margin.top + "px")
          .style("left", event.pageX + margin.left + "px");
      })
      .on("mouseout", function () {
        d3.select(this).style("opacity", 0);
      });
  });

  return (
    <section className="labor_productivity">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/productivity-isnt-everything-but-in-the-long-run-it-is-almost-everything"
          >
            America 2021: Rebuilding lives and livelihoods after COVID-19
          </a>
        </h2>
      </header>
      <div>
        <div id="chart"></div>
      </div>
    </section>
  );
}
