import React, { useEffect, useState } from "react";
import * as d3 from "d3";
import { dataset } from "./gpd_per_capita";

import "./VietnamEducation.css";

export default function VietnamEducation() {
  const [colors] = useState(["#7BE6FB", "#00A9F9", "#051C2C"]);

  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 320,
      height = 220;

    let tooltip = d3
      .select(".vietnam_education #chart")
      .append("div")
      .attr("class", "tooltip")
      .style("font-size", ".675rem")
      .style("color", "white")
      .style("opacity", "0")
      .style("position", "absolute")
      .style("padding", ".5em .8em")
      .style("background", "#051C2C");

    let svg = d3
      .select(".vietnam_education #chart")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${2 * margin.right}, ${margin.top}) `);

    let xScale = d3
      .scaleSqrt()
      .domain([0, 150000])
      .range([0, width - 2 * margin.left])
      .exponent(0.6);
    let yScale = d3.scaleLinear().domain([300, 600]).range([height, 0]);

    let xAxis = d3
      .axisBottom(xScale)
      .tickFormat((d) => {
        if (d >= 50000) {
          return d / 1000;
        }

        return d;
      })
      .tickPadding(8)
      .tickSize(-5)
      .tickValues([0, 50000, 100000, 150000]);

    let yAxis = d3
      .axisLeft(yScale)
      .tickPadding(8)
      .tickSize(0)
      .tickValues([300, 400, 500, 600]);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${0}, ${height}) `);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0}, ${0}) `);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);

    let polylineGroup = chartGroup
      .append("g")
      .attr("class", "polylineGroup")
      .attr("transform", `translate(0, ${0}) `);

    polylineGroup
      .append("polyline")
      .attr("stroke", "#d8d8d8")
      .attr("fill", "none")
      .attr(
        "points",
        `${xScale(0)} ${yScale(600)}, ${xScale(150000)} ${yScale(
          600
        )}, ${xScale(150000)} ${yScale(300)}`
      );

    chartGroup
      .append("line")
      .attr("stroke", "black")
      .attr("stroke-dasharray", "3")
      .attr("x1", xScale(0))
      .attr("y1", yScale(420))
      .attr("x2", xScale(150000))
      .attr("y2", yScale(580));

    let rating = ["EXCELLENT", "GREAT", "GOOD", "FAIR", "POOR"];

    let ratingGroup = chartGroup
      .append("g")
      .attr("class", "ratingGroup")
      .attr("transform", `translate(0, ${0}) `);

    ratingGroup
      .selectAll("text")
      .data(rating)
      .enter()
      .append("text")
      .text((d) => d)
      .attr("text-anchor", "end")
      .attr("font-size", ".5rem")
      .attr("x", xScale(140000))
      .attr("y", (d, i) => {
        if (d !== "POOR") {
          return yScale(580 - i * 2 * margin.top);
        } else {
          return yScale(370);
        }
      });

    ratingGroup
      .selectAll("line")
      .data(rating)
      .enter()
      .append("line")
      .attr("stroke", (d) => d !== "POOR" && "#d8d8d8")
      .attr("x1", xScale(0))
      .attr("y1", (d, i) => {
        return yScale(570 - i * 2 * margin.top);
      })
      .attr("x2", xScale(150000))
      .attr("y2", (d, i) => {
        return yScale(570 - i * 2 * margin.top);
      });

    chartGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("x1", xScale(50000))
      .attr("y1", yScale(470))
      .attr("x2", xScale(50000))
      .attr("y2", yScale(370));

    let scatterPlot = chartGroup
      .append("g")
      .attr("class", "scatterPlot")
      .attr("transform", `translate(0, ${0}) `);

    scatterPlot
      .selectAll("line")
      .data(dataset)
      .enter()
      .append("line")
      .attr("stroke", (d) => {
        return d.country === "Vietnam" ? "#d8d8d8" : null;
      })
      .attr("x1", (d) => xScale(d.gpd))
      .attr("y1", (d) => yScale(d.score))
      .attr("x2", (d, i) => xScale(d.gpd))
      .attr("y2", (d) => yScale(d.score) - margin.bottom / 2);

    scatterPlot
      .selectAll("circle")
      .data(dataset)
      .enter()
      .append("circle")
      .attr("fill", (d) => {
        if (d.region === "Asia & Pacific") {
          return colors[0];
        } else if (d.region === "South/Latin America") {
          return colors[1];
        }

        return colors[2];
      })
      .attr("cx", (d) => {
        return xScale(d.gpd);
      })
      .attr("cy", (d) => {
        return yScale(d.score);
      })
      .attr("r", "3")
      .on("mouseover", function (event, d) {
        tooltip.transition().duration(1000).style("opacity", 1);

        tooltip
          .html(
            `<span>Country: ${d.country}</span>
          <span>Score: ${d.score}</span>
          <span>GDP: ${d.gpd}</span>
          `
          )
          .style("top", event.pageY - 3.5 * margin.top + "px")
          .style("left", event.pageX - margin.left + "px");
      });

    chartGroup
      .append("foreignObject")
      .attr("width", 120)
      .attr("height", 100)
      .attr("font-size", ".5rem")
      .attr("x", xScale(35000))
      .attr("y", yScale(365))
      .append("xhtml:div")
      .text(
        "Countries below trend line underperform in PISA science tests, given their GDPs"
      )
      .style("line-height", ".6rem");

    chartGroup
      .append("foreignObject")
      .attr("width", 60)
      .attr("height", 250)
      .attr("font-size", ".5rem")
      .attr("font-weight", 500)
      .attr("transform", `translate(${-2 * margin.right}, ${yScale(470)}) `)
      .append("xhtml:div")
      .text("2018 PISA science test, mean score")
      .style("line-height", ".6rem")
      .style("width", 100)
      .style("text-align", "center");

    chartGroup
      .append("foreignObject")
      .attr("width", 100)
      .attr("height", 250)
      .attr("font-size", ".5rem")
      .attr("font-weight", 500)
      .attr("transform", `translate(${xScale(30000)}, ${height + margin.top}) `)
      .append("xhtml:div")
      .text("GDP per capita, purchasing-power parity, $ thousand")
      .style("line-height", ".6rem")
      //   .style("width", 100);
      .style("text-align", "center");

    scatterPlot
      .selectAll("text")
      .data(dataset)
      .enter()
      .append("text")
      .text((d) => {
        return d.country === "Vietnam" ? d.country : null;
      })
      .attr("text-anchor", "middle")
      .attr("font-size", ".5rem")
      .attr("x", (d) => xScale(d.gpd))
      .attr("y", (d, i) => yScale(d.score) - margin.bottom / 2);

    let labels = ["Asia-Pacific", "Latin America", "Other"];

    let labelsGroup = chartGroup
      .append("g")
      .attr("class", "labelsGroup")
      .attr("transform", `translate(0, ${0}) `);

    labelsGroup
      .selectAll("text")
      .data(labels)
      .enter()
      .append("text")
      .text((d) => d)
      .attr("x", xScale(170000))
      .attr("y", (d, i) => {
        return yScale(580 - i * margin.top);
      });

    labelsGroup
      .selectAll("circle")
      .data(colors)
      .enter()
      .append("circle")
      .attr("fill", (d, i) => {
        return colors[i];
      })
      .attr("cx", xScale(162000))
      .attr("cy", (d, i) => {
        return yScale(584 - i * margin.top);
      })
      .attr("r", "3");
  });
  return (
    <section className="vietnam_education">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/vietnams-education-system-significantly-exceeds-expectations"
          >
            What will it take to achieve Vietnam’s long-term growth aspirations?
          </a>
        </h2>
      </header>
      <div>
        <div id="chart"></div>
        {/* <ul className="labels">
          <li>
            <span style={{ backgroundColor: colors[0] }}></span>
            <span>Asia-Pacific</span>
          </li>
          <li>
            <span style={{ backgroundColor: colors[1] }}></span>
            <span>Latin America</span>
          </li>
          <li>
            <span style={{ backgroundColor: colors[2] }}></span>
            <span>Other</span>
          </li>
        </ul> */}
      </div>
    </section>
  );
}
