import React, { useEffect, useState } from "react";
import * as d3 from "d3";

import "./CovidExit.css";

export default function CovidExit() {
  const [colors] = useState(["#051C2C", "#5971E7", "#00A8F3"]);
  const [dasharray] = useState(["0", "3", "0"]);
  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 600,
      height = 300;

    let svg = d3
      .select(".covid_exit #chart")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.left,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    let dataset = [
      {
        id: 1,
        year: 2007,
        resilients: 100,
        sp_500: 100,
        non_resilients: 100,
      },
      {
        id: 2,
        year: 2008,
        resilients: 65,
        sp_500: 60,
        non_resilients: 50,
      },
      {
        id: 3,
        year: 2009,
        resilients: 100,
        sp_500: 65,
        non_resilients: 60,
      },
      {
        id: 4,
        year: 2010,
        resilients: 145,
        sp_500: 75,
        non_resilients: 70,
      },
      {
        id: 5,
        year: 2011,
        resilients: 150,
        sp_500: 75,
        non_resilients: 62,
      },
      {
        id: 6,
        year: 2012,
        resilients: 175,
        sp_500: 110,
        non_resilients: 80,
      },
      {
        id: 7,
        year: 2013,
        resilients: 230,
        sp_500: 140,
        non_resilients: 120,
      },
      {
        id: 8,
        year: 2014,
        resilients: 250,
        sp_500: 150,
        non_resilients: 120,
      },
      {
        id: 9,
        year: 2015,
        resilients: 240,
        sp_500: 150,
        non_resilients: 115,
      },
      {
        id: 10,
        year: 2016,
        resilients: 260,
        sp_500: 170,
        non_resilients: 125,
      },
      {
        id: 11,
        year: 2017,
        resilients: 310,
        sp_500: 200,
        non_resilients: 140,
      },
    ];

    const stacked = d3.stack().keys(["resilients", "sp_500", "non_resilients"])(
      dataset
    );

    let areaChart = d3
      .area()
      .x((d) => {
        return xScale(d.data.year);
      })
      .y0(height)
      .y1((d) => {
        return yScale(d[1] - d[0]);
      });

    let lineChart = d3
      .line()
      .x((d) => {
        return xScale(d.data.year);
      })
      .y((d) => {
        return yScale(d[1] - d[0]);
      });

    let xScale = d3
      .scaleBand()
      .domain(dataset.map((d) => d.year))
      .range([0, width - margin.left]);
    let yScale = d3.scaleLinear().domain([50, 350]).range([height, 0]);

    let xAxis = d3
      .axisBottom(xScale)
      .tickPadding(15)
      .tickValues([2007, 2009, 2011, 2013, 2015, 2017]);
    let yAxis = d3
      .axisLeft(yScale)
      .tickValues([50, 100, 150, 200, 250, 300, 350]);

    let stackedGroup = chartGroup
      .append("g")
      .attr("class", "stackedGroup")
      .attr("transform", `translate(${margin.right - 5}, ${0}) `);

    let stackedSeries = stackedGroup
      .selectAll("g.series")
      .data(stacked)
      .enter()
      .append("g")
      .classed("series", true)
      .attr("fill", (d, i) => {
        return colors[i];
      });

    stackedSeries.append("path").attr("d", areaChart);
    stackedSeries
      .style("stroke-dasharray", (d, i) => dasharray[i])
      .attr("stroke-width", "2")
      .append("path")
      .style("fill", "none")
      .style("stroke", (d, i) => colors[i])
      .attr("d", lineChart);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${0}, ${height}) `);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(${0}, ${0}) `);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);

    let xAxisGrid = d3.axisBottom(xScale).tickFormat("");

    let xAxisGridGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridGroup")
      .attr("transform", `translate(${0}, ${height}) `);

    xAxisGrid(xAxisGridGroup);

    let polylineGroup = chartGroup
      .append("g")
      .attr("class", "polylineGroup")
      .attr("transform", `translate(${margin.right - 5}, ${0}) `);

    polylineGroup
      .append("polyline")
      .attr("stroke", "black")
      .attr("fill", "none")
      .attr(
        "points",
        `${xScale(2007)} ${yScale(50)}, 0 ${yScale(150)}, ${xScale(
          2009
        )} ${yScale(150)}`
      );

    polylineGroup
      .append("polyline")
      .attr("stroke", "black")
      .attr("fill", "none")
      .attr(
        "points",
        `${xScale(2009)} ${yScale(50)}, ${xScale(2009)} ${yScale(
          200
        )}, ${xScale(2011)} ${yScale(200)}`
      );

    polylineGroup
      .append("polyline")
      .attr("stroke", "black")
      .attr("fill", "none")
      .attr(
        "points",
        `${xScale(2011)} ${yScale(50)}, ${xScale(2011)} ${yScale(
          340
        )}, ${xScale(2017)} ${yScale(340)}, ${xScale(2017)} ${yScale(50)}`
      );

    let polylineTextGroup = chartGroup
      .append("g")
      .attr("class", "polylineTextGroup")
      .attr("transform", `translate(${0}, ${0}) `);

    polylineTextGroup
      .append("text")
      .text("Downturn")
      .attr("text-anchor", "middle")
      .attr("dx", 25)
      .attr("x", xScale(2008))
      .attr("y", yScale(130));

    polylineTextGroup
      .append("text")
      .text("Recovery")
      .attr("text-anchor", "middle")
      .attr("dx", 25)
      .attr("x", xScale(2010))
      .attr("y", yScale(180));

    polylineTextGroup
      .append("text")
      .text("Growth")
      .attr("text-anchor", "middle")
      .attr("dx", 25)
      .attr("x", xScale(2014))
      .attr("y", yScale(320));

    //   Labels

    polylineTextGroup
      .append("text")
      .text("Resilients")
      .attr("dx", 35)
      .attr("x", xScale(2017))
      .attr("y", yScale(310));

    polylineTextGroup
      .append("text")
      .text("S&P 500")
      .attr("dx", 35)
      .attr("x", xScale(2017))
      .attr("y", yScale(200));

    polylineTextGroup
      .append("text")
      .text("Nonresilients")
      .attr("dx", 35)
      .attr("x", xScale(2017))
      .attr("y", yScale(140));
  });
  return (
    <section className="covid_exit">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/companies-that-take-steps-to-improve-resiliency-now-could-see-benefits-for-years"
          >
            As you return from the summer break, can you lead toward a
            COVID-Exit?
          </a>
        </h2>
      </header>
      <div>
        <div id="chart"></div>
      </div>
    </section>
  );
}
