import React, { useEffect, useState } from "react";
import * as d3 from "d3";

import "./BeyondCovid.css";

export default function BeyondCovid() {
  const [colors] = useState(["#00A8F3", "#051C2C"]);
  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 600,
      height = 400,
      xBandwidth = 18;

    let svg = d3
      .select(".beyond_covid #chart")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.left,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    let dataset = [
      {
        id: 1,
        digital_channel: "Online banking",
        increase: 42,
        decrease: -5,
      },
      {
        id: 2,
        digital_channel: "Mobile banking",
        increase: 42,
        decrease: -5,
      },
      {
        id: 3,
        digital_channel: "Mobile payment (e.g. SnapScan/zapper)",
        increase: 24,
        decrease: -28,
      },
      {
        id: 4,
        digital_channel: "Visiting a physical branch to make a transaction",
        increase: 9,
        decrease: -45,
      },
      {
        id: 5,
        digital_channel:
          "Phoning a physical branch to talk to an adviser or staff",
        increase: 10,
        decrease: -37,
      },
    ];

    let stacked = d3.stack().keys(["increase", "decrease"])(dataset);

    let columns = dataset.map((d) => d.digital_channel);

    let xScale = d3.scaleBand().domain(columns).range([0, width]);
    let yScale = d3
      .scaleLinear()
      .domain([-45, 42])
      .range([height - 2.5 * margin.bottom, margin.bottom]);

    chartGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("x1", "0")
      .attr("y1", margin.top)
      .attr("x2", "0")
      .attr("y2", yScale(-45));

    chartGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("x1", 0)
      .attr("y1", yScale(0) + xBandwidth)
      .attr("x2", xScale(columns[columns.length - 1]) + 4 * margin.right)
      .attr("y2", yScale(0) + xBandwidth);

    chartGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("x1", 0)
      .attr("y1", yScale(-60))
      .attr("x2", xScale(columns[columns.length - 1]) + 4 * margin.right)
      .attr("y2", yScale(-60));

    chartGroup
      .append("text")
      .text("MORE")
      .attr("font-weight", "500")
      .attr("text-anchor", "middle")
      .attr("x", 0)
      .attr("y", yScale(52));

    chartGroup
      .append("text")
      .text("LESS")
      .attr("font-weight", "500")
      .attr("text-anchor", "middle")
      .attr("x", 0)
      .attr("y", yScale(-52));

    let net = chartGroup
      .append("g")
      .attr("class", "net")
      .attr("transform", `translate(${0}, ${height - margin.bottom / 2}) `)
      .append("text");

    let net_text = ["Net change,", "percentage", "points"];

    net_text.map((text) =>
      net.append("tspan").text(text).attr("x", -margin.left).attr("dy", 15)
    );

    let symbol = d3.symbol().type(d3.symbolTriangle).size(80);

    chartGroup
      .append("path")
      .attr("fill", "#d8d8d8")
      .attr("transform", `translate(${0}, ${margin.top}) `)
      .attr("d", symbol);

    chartGroup
      .append("g")
      .attr("transform", `translate(${0}, ${height - 4 * margin.top}) `)
      .append("path")
      .attr("fill", "#d8d8d8")
      .attr("transform", `rotate(180)`)
      .attr("d", symbol);

    let stackGroup = chartGroup
      .append("g")
      .attr("class", "stacked")
      .attr("transform", `translate( ${2 * margin.right}, ${0}) `);

    let stackedGroup = stackGroup
      .selectAll("g.series")
      .data(stacked)
      .enter()
      .append("g")
      .classed("series", true)
      .style("fill", function (d, i) {
        return colors[i];
      });

    stackedGroup
      .selectAll("rect")
      .data(function (d) {
        return d;
      })
      .enter()
      .append("rect")
      .attr("width", xScale.bandwidth() / 2.5)
      .attr("height", (d) => {
        return Math.abs(yScale(d[1] - d[0]) - yScale(0)) + xBandwidth;
      })
      .attr("x", (d) => {
        return xScale(d.data.digital_channel);
      })
      .attr("y", (d) => {
        if (d[1] <= 0) {
          return yScale(0) + xBandwidth;
        } else {
          return yScale(d[1] - d[0]);
        }
      });

    let textGroup = stackedGroup
      .append("g")
      .attr("transform", `translate(${margin.right - 5}, ${margin.top}) `);

    textGroup
      .selectAll("text")
      .data((d) => d)
      .enter()
      .append("text")
      .text((d) => {
        return d[1] - d[0];
      })
      .attr("text-anchor", "middle")
      .attr("x", (d) => {
        return xScale(d.data.digital_channel);
      })
      .attr("y", (d) => {
        return yScale(d[1] - d[0]);
      });

    let xAxistTextGroup = stackedGroup
      .append("g")
      .attr("class", "xAxistTextGroup")
      .attr("transform", `translate(${margin.right}, ${height + margin.top}) `);

    xAxistTextGroup
      .selectAll("text")
      .data((d) => d)
      .enter()
      .append("text")
      .text((d) => {
        if (d.data.increase + d.data.decrease > 0) {
          return `+${d.data.increase + d.data.decrease}`;
        }

        return d.data.increase + d.data.decrease;
      })
      .attr("text-anchor", "middle")
      .attr("x", (d) => {
        return xScale(d.data.digital_channel);
      })
      .attr("y", (d) => {
        return 0;
      });

    let labelTextGroup = stackedGroup
      .append("g")
      .attr("class", "labelTextGroup")
      .attr(
        "transform",
        `translate(${-margin.right / 2}, ${-margin.top / 2}) `
      );

    let foreignObject = labelTextGroup
      .selectAll("foreignObject")
      .data(columns)
      .enter()
      .append("foreignObject")
      .text("")
      .style("width", "75px")
      .style("height", "120px")
      .attr("x", (d) => {
        return xScale(d);
      });

    foreignObject
      .append("xhtml:div")
      .text((d) => {
        return d;
      })
      .style("text-align", "center")
      .style("font-size", ".875rem")
      .style("white-space", "text-wrap");
  });

  return (
    <section className="beyond_covid">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/covid-19-has-made-south-africans-much-more-comfortable-with-online-banking"
          >
            Beyond COVID-19: Charting the road to recovery for South African
            insurers
          </a>
        </h2>
      </header>
      <div>
        <div id="chart"></div>
      </div>
    </section>
  );
}
