import React, { useEffect } from "react";
import * as d3 from "d3";

import "./FashionIndustry.css";

export default function FashionIndustry() {
  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 400,
      height = 400;

    let tooltip = d3
      .select("#fashion_industry")
      .append("div")
      .style("position", "absolute")
      .style("padding", "0 .8em")
      .style("background", "white")
      .style("opacity", 0);

    let svg = d3
      .select("#fashion_industry")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 5 * margin.right,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    let dataset = [
      { year: 2016, profit: 100 },
      { year: 2017, profit: 140 },
      { year: 2018, profit: 160 },
      { year: 2019, profit: 169 },
      { year: 2020, profit: 11 },
    ];

    const xScale = d3
      .scaleBand()
      .domain(dataset.map((d) => d.year))
      .range([0, width]);

    const yScale = d3.scaleLinear().domain([0, 200]).range([height, 0]);

    let xAxis = d3.axisBottom(xScale).ticks(5).tickSize(0).tickPadding(10);
    let yAxis = d3.axisLeft(yScale).ticks(5).tickSize(-width);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(0, ${height})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(0, 0)`);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);

    let dataGroup = chartGroup
      .append("g")
      .attr("class", "dataGroup")
      .attr("transform", `translate(0, 0)`)
      .selectAll("rect")
      .data(dataset)
      .enter()
      .append("rect")
      .attr("fill", (d) => (d.year === 2020 ? "#00A8F3" : "#051C2C"))
      .attr("width", xScale.bandwidth())
      .attr("height", 0)
      .attr("x", (d, i) => xScale(d.year))
      .attr("y", height)

      .on("mouseover", function (event, d) {
        tooltip.transition().duration(200).style("opacity", 1);

        tooltip
          .html(d.profit)
          .style("left", event.pageX - 35 + "px")
          .style("top", event.pageY - 30 + "px");

        d3.select(this)
          .transition()
          .duration(1000)
          .ease(d3.easeLinear)
          .style("fill", "#00A8F3");
      })
      .on("mouseout", function () {
        d3.select(this)
          .transition()
          .duration(1000)
          .ease(d3.easeLinear)
          .style("fill", (d) => (d.year === 2020 ? "#00A8F3" : "#051C2C"));
      });

    dataGroup
      .transition()
      .duration(500)
      .ease(d3.easeLinear)
      .delay(function (d, i) {
        return i * 20;
      })
      .attr("height", (d) => height - yScale(d.profit))
      .attr("y", (d) => yScale(d.profit));

    let textGroup = chartGroup
      .append("g")
      .attr("class", "textGroup")
      .attr("transform", `translate(${xScale.bandwidth() / 2}, ${-5})`);

    textGroup
      .selectAll("text")
      .data(dataset)
      .enter()
      .append("text")
      .text((d) => (d.year === 2019 || d.year === 2020 ? d.profit : null))
      .attr("text-anchor", "middle")
      .attr("x", (d, i) => xScale(d.year))
      .attr("y", (d) => yScale(d.profit));

    chartGroup
      .append("line")
      .attr("class", "dashed-line")
      .attr("stroke", "#d8d8d8")
      .attr("stroke-dasharray", "1")
      .attr("x1", "0")
      .attr("y1", yScale(100))
      .attr("x2", width)
      .attr("y2", yScale(100));

    const groupTicks = chartGroup
      .append("g")
      .attr("class", "ticks")
      .attr("transform", `translate(0, ${10})`);

    groupTicks
      .selectAll("line")
      .data(dataset)
      .enter()
      .append("line")
      .attr("class", "dashed-line")

      .attr("x1", (d) => xScale(d.year))
      .attr("y1", yScale(11 / 2))
      .attr("x2", (d) => xScale(d.year))
      .attr("y2", yScale(0));

    const horizonalGrid = svg
      .append("g")
      .attr("class", "horizonalGrid")
      .attr("transform", `translate(${width - margin.left * 2}, ${0})`);

    horizonalGrid
      .append("line")
      .attr("stroke-width", "2")
      .attr("x1", margin.left * 1.5)
      .attr("y1", yScale(169) + margin.top)
      .attr("x2", width + 200)
      .attr("y2", yScale(169) + margin.top);

    horizonalGrid
      .append("line")
      .attr("stroke-width", "2")
      .attr("x1", margin.left * 3.5)
      .attr("y1", yScale(11) + margin.top)
      .attr("x2", width)
      .attr("y2", yScale(11) + margin.top);

    const verticalGrid = svg
      .append("g")
      .attr("class", "horizonalGrid")
      .attr("transform", `translate(${width + margin.left * 3.5}, ${0})`);

    verticalGrid
      .append("line")
      .attr("stroke", "red")
      .attr("stroke-width", "2")
      .attr("x1", xScale(11))
      .attr("y1", yScale(169) + margin.top)
      .attr("x2", xScale(11))
      .attr("y2", yScale(11) + margin.top);

    var symbolGenerator = d3.symbol().type(d3.symbolTriangle).size(80);

    svg
      .append("g")
      .attr("class", "horizonalGrid")
      .attr(
        "transform",
        `translate(${width + margin.left * 3.5}, ${
          yScale(11) + margin.top / 2
        })`
      )
      .append("path")
      .attr("fill", "#d8d8d8")
      .attr("transform", `rotate(180)`)
      .attr("d", symbolGenerator());
  });

  return (
    <section>
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/fashion-industrys-profits-hemmed-in-by-the-covid-19-pandemic"
          >
            The State of Fashion 2021: In search of promise in perilous times
          </a>
        </h2>
      </header>
      <div id="fashion_industry" className="fashion_industry">
        <div className="heading">
          <h3>-93%</h3>
          <p>drop in economic profit from 2019 to 2020</p>
        </div>
      </div>
    </section>
  );
}
