import React, { useEffect, useState } from "react";
import * as d3 from "d3";

import "./WomenWorkplace.css";

export default function WomenWorkplace() {
  const [colors] = useState(["#d4d4d4", "#7be6fb", "#00A8F3", "#051C2C"]);
  useEffect(() => {
    const margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 600,
      height = 400;

    // let tooltip = d3
    //   .select("#fashion_industry")
    //   .append("div")
    //   .style("position", "absolute")
    //   .style("padding", "0 .8em")
    //   .style("background", "white")
    //   .style("opacity", 0);

    let svg = d3
      .select(".women_workplace #chart")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.left,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    let dataset = [
      {
        id: 1,
        employees: "Fathers",
        excluded: 10,
        presseured: 24,
        burned_out: 28,
        exhausted: 31,
      },
      {
        id: 2,
        employees: "Mothers",
        excluded: 10,
        presseured: 28,
        burned_out: 32,
        exhausted: 39,
      },
      {
        id: 3,
        employees: "Senior level men",
        excluded: 8,
        presseured: 27,
        burned_out: 29,
        exhausted: 41,
      },
      {
        id: 4,
        employees: "Senior level women",
        excluded: 11,
        presseured: 28,
        burned_out: 29,
        exhausted: 55,
      },
      {
        id: 5,
        employees: "Black men",
        excluded: 12,
        presseured: 21,
        burned_out: 22,
        exhausted: 29,
      },
      {
        id: 6,
        employees: "Black women",
        excluded: 18,
        presseured: 27,
        burned_out: 32,
        exhausted: 40,
      },
    ];

    let stack = d3
      .stack()
      .keys(["excluded", "presseured", "burned_out", "exhausted"]);

    let stackedSeries = stack(dataset);

    const xScale = d3
      .scaleBand()
      .domain(dataset.map((d) => d.employees))
      .range([0, width - margin.right / 2]);

    const yScale = d3.scaleLinear().domain([0, 60]).range([height, 0]);

    let xAxis = d3.axisBottom(xScale).tickPadding(15).tickSize(0);

    let yAxis = d3
      .axisLeft(yScale)
      .tickValues([0, 10, 20, 30, 40, 50, 60])
      .tickSize(-width + margin.right / 2)
      .tickPadding(20);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(0, ${height})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(0, 0)`);

    let yAxisRightGroup = chartGroup
      .append("g")
      .attr("class", "yAxisRightGroup")
      .attr("transform", `translate(${width + margin.left}, 0)`);

    xAxis(xAxisGroup);
    yAxis(yAxisGroup);
    yAxis(yAxisRightGroup);

    let plotsLineGroup = chartGroup
      .append("g")
      .selectAll("g.series")
      .data(stackedSeries)
      .enter()
      .append("g")
      .classed("series", true)
      .style("fill", function (d, i) {
        return colors[i];
      })
      .attr("transform", `translate(${margin.right + 15}, 0)`);

    plotsLineGroup
      .selectAll("line")
      .data(function (d) {
        return d;
      })
      .enter()
      .append("line")
      .attr("stroke", "black")
      .attr("x1", function (d, i) {
        return xScale(d.data.employees);
      })
      .attr("y1", (d) => {
        return (
          height -
          (d.data.excluded +
            d.data.presseured +
            d.data.burned_out +
            d.data.exhausted)
        );
      })
      .attr("x2", function (d, i) {
        return xScale(d.data.employees);
      })
      .attr("y2", function (d, i) {
        return yScale(d[1] - d[0]);
      });

    let plotsGroup = chartGroup
      .append("g")
      .selectAll("g.series")
      .data(stackedSeries)
      .enter()
      .append("g")
      .classed("series", true)
      .style("fill", function (d, i) {
        return colors[i];
      })
      .attr("transform", `translate(${margin.right + 15}, 0)`);

    plotsGroup
      .selectAll("circle")
      .data(function (d) {
        return d;
      })
      .enter()
      .append("circle")
      .attr("cx", function (d, i) {
        return xScale(d.data.employees);
      })
      .attr("cy", function (d, i) {
        return yScale(d[1] - d[0]);
      })
      .attr("r", "5");

    let xScaleGrid = d3
      .scaleBand()
      .domain([1])
      .range([0, width / 3 - margin.left / 2]);

    let xAxisGrid = d3
      .axisBottom(xScaleGrid)
      .tickFormat("")
      .tickSize(10)
      .ticks(3);

    let gridFunc = (left = 0) => {
      return chartGroup
        .append("g")
        .attr("class", "xAxisGridGroup")
        .attr("transform", `translate(${left}, ${height})`);
    };

    xAxisGrid(gridFunc());
    xAxisGrid(gridFunc(width / 3));
    xAxisGrid(gridFunc(width / 1.5));

    d3.selectAll(".women_workplace .xAxisGroup .tick text")
      .text((d) => {
        if (d.split(" ").length >= 3) {
          return `${d.split(" ")[0]} ${d.split(" ")[1]}`;
        }

        return d;
      })
      .attr("text-anchor", "middle")
      .append("tspan")
      .text((d) => {
        if (d.split(" ").length >= 3) {
          return `${d.split(" ")[2]}`;
        }

        return null;
      })
      .attr("text-anchor", "middle")
      .attr("x", 0)
      .attr("dy", 15);
  });
  return (
    <section className="women_workplace">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/women-are-feeling-more-pressure-at-work-due-to-the-covid-19-crisis-than-men-are"
          >
            Women in the Workplace 2020
          </a>
        </h2>
      </header>
      <div>
        <div id="chart">
          <div className="labels">
            <ul>
              <li>
                <span style={{ backgroundColor: colors[0] }}></span>
                <span>Excluded</span>
              </li>
              <li>
                <span style={{ backgroundColor: colors[1] }}></span>
                <span>Pressured to work more</span>
              </li>
              <li>
                <span style={{ backgroundColor: colors[2] }}></span>
                <span>Burned Out</span>
              </li>
              <li>
                <span style={{ backgroundColor: colors[3] }}></span>
                <span>Exhausted</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}
