import React, { useEffect } from "react";
import * as d3 from "d3";

import "./LatinAmerica.css";

export default function LatinAmerica() {
  useEffect(() => {
    let margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 400,
      height = 180;

    // let labels = d3
    //   .select(".latin_america #product")
    //   .append("div")
    //   .attr("class", "labels")
    //   .style("position", "absolute")
    //   .style("width", "84%")
    //   .style("top", margin.top)
    //   .style("padding", margin.left);

    // labels.append("h4").text("In demand for companies' products or services");

    let svg = d3
      .select(".latin_america #product")
      .append("svg")
      .attr("viewBox", [0, 0, width + margin.left, height + 2 * margin.bottom]);

    let dataset = {
      product: [
        { date: "2020/12/01", value: 75, total: 61 },
        { date: "2021/01/01", value: 54, total: 57 },
      ],
      profit: [
        { date: "2020/12/01", value: 72, total: 56 },
        { date: "2021/01/01", value: 48, total: 53 },
      ],
    };

    let formatDate = d3.timeFormat("%b %Y");

    const scaleFunction = (propertName, left, value = 0) => {
      dataset[propertName].forEach(
        (d) => (d.date = formatDate(new Date(d.date)))
      );

      let xScale = d3
        .scaleBand()
        .domain(d3.extent(dataset[propertName], (d) => d.date))
        .range([0, width / 2 - margin.right]);

      let yScale = d3
        .scaleLinear()
        .domain([0, 75])
        .range([height, 0])
        .nice(false);

      let xAxis = d3.axisBottom(xScale).tickSize(0).tickPadding(10);
      let yAxisLeft = d3
        .axisLeft(yScale)
        .ticks(3)
        .tickValues([0, 25, 50, 75])
        .tickSize(-width / 2 + margin.right)
        .tickSize(0);
      let yAxisRightGrid = d3
        .axisLeft(yScale)
        .tickFormat("")
        .ticks(4)
        .tickValues([0, 25, 50, 75])
        .tickSize(width / 2 - margin.left);

      let chartGroup = svg
        .append("g")
        .attr("class", propertName)
        .attr("transform", `translate(${left}, ${margin.top}) `);
      let xAxisGroup = chartGroup
        .append("g")
        .attr("class", "xAxisGroup")
        .attr("transform", `translate(${0}, ${height}) `);

      let yAxisLeftGroup = chartGroup
        .append("g")
        .attr("class", "yAxisLeftGroup")
        .attr("transform", `translate(${value}, ${0}) `);

      let yAxisRightGridGroup = chartGroup
        .append("g")
        .attr("class", "yAxisRightGroup")
        .attr("transform", `translate(${width / 2 - margin.left + 2}, ${0}) `);

      // xAxis(xAxisProfitGroup);
      // yAxisRight(yAxisRightGroup);

      xAxis(xAxisGroup);
      yAxisLeft(yAxisLeftGroup);
      yAxisRightGrid(yAxisRightGridGroup);

      let profuctGroup = chartGroup
        .append("g")
        .attr("class", "profuctGroup")
        .attr("transform", `translate(${margin.left}, ${0}) `);

      let line = d3
        .line()
        .x((d) => xScale(d.date))
        .y((d) => yScale(d.value));

      profuctGroup
        .append("path")
        .attr("stroke", "#00A8F3")
        .attr("d", line(dataset[propertName]));

      profuctGroup
        .selectAll("circle")
        .data(dataset[propertName])
        .enter()
        .append("circle")
        .attr("fill", "#00A8F3")
        .attr("stroke", "#00A8F3")
        .attr("cx", (d) => xScale(d.date))
        .attr("cy", (d) => yScale(d.value))
        .attr("r", "4");

      let profuctTotalGroup = chartGroup
        .append("g")
        .attr("class", "profuctTotalGroup")
        .attr("transform", `translate(${margin.left}, ${0}) `);

      profuctTotalGroup
        .append("path")
        .attr("stroke", "#00A8F3")
        .attr("stroke-dasharray", 1)
        .attr("d", line.y((d) => yScale(d.total))(dataset[propertName]));

      profuctTotalGroup
        .selectAll("circle")
        .data(dataset[propertName])
        .enter()
        .append("circle")
        .attr("fill", "white")
        .attr("stroke", "#00A8F3")
        .attr("cx", (d) => xScale(d.date))
        .attr("cy", (d) => yScale(d.total))
        .attr("r", "4");
    };

    scaleFunction("product", margin.left);
    scaleFunction(
      "profit",
      width / 2 + 2 * margin.right,
      width / 2 - margin.right
    );

    d3.selectAll(".latin_america .profit .yAxisLeftGroup text").attr(
      "text-anchor",
      "start"
    );
  });

  return (
    <section className="latin_america">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/latin-american-executives-confidence-in-the-economy-drops"
          >
            Latin American executives’ confidence in the economy drops
          </a>
        </h2>
      </header>
      <div>
        <div id="product">
          <div className="labels">
            <h4>In demand for companies' products or services</h4>
            <h4>In companies' profits</h4>
          </div>
        </div>
      </div>
    </section>
  );
}
