import React, { useEffect } from "react";
import { dataset } from "./gapminder._lifeexpection";
import * as d3 from "d3";

import "./LifeExpectation.css";

export default function LifeExpectation() {
  //   console.log(convert);
  useEffect(() => {
    let margin = { top: 20, right: 30, bottom: 30, left: 40 },
      width = 600,
      height = 400;

    let tooltip = d3
      .select("#life_exp")
      .append("div")
      .attr("class", "tooltip")
      .style("font-size", ".75rem")
      .style("color", "white")
      .style("opacity", "0")
      .style("position", "absolute")
      .style("padding", ".5em .8em")
      .style("background", "#051C2C");

    let svg = d3
      .select("#life_exp")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 2 * margin.bottom,
      ]);

    let chartGroup = svg
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top}) `);

    const grandTotal = [];

    let years = dataset.map((data, i) => {
      return Object.keys(data);
    })[0];

    const calcMean = (value) =>
      d3.mean(
        dataset.map((d, i, array) => {
          return d[value];
        })
      );

    for (let i = 0; i < years.length; i++) {
      if (years[i] !== "country") {
        grandTotal.push({
          year: parseInt(years[i]),
          mean: calcMean(years[i]),
        });
      }
    }
    let important_years = grandTotal.filter((expectancy) => {
      if (
        expectancy.year === 1800 ||
        expectancy.year === 1918 ||
        expectancy.year === 1944 ||
        expectancy.year === 1958 ||
        expectancy.year === 2020
      ) {
        return expectancy;
      }

      return null;
    });

    const xScale = d3
      .scaleLinear()
      .domain(d3.extent(important_years, (d) => parseInt(d.year)))
      .range([0, width - margin.left]);
    const yScale = d3.scaleLinear().domain([0, 80]).range([height, 0]);

    let xAxis = d3
      .axisBottom(xScale)
      .tickFormat((d) => {
        return parseInt(d);
      })
      .ticks(5)
      .tickPadding(10)
      .tickSize(10);
    let xAxisGrid = d3
      .axisBottom(xScale)
      .ticks(20)
      .tickFormat("")
      .tickPadding(10)
      .tickSize(10);
    let yAxis = d3.axisLeft(yScale).ticks(5).tickSize(0);
    let yAxisGrid = d3
      .axisLeft(yScale)
      .tickFormat("")
      .ticks(10)
      .tickSize(-width + margin.left);

    let xAxisGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGroup")
      .attr("transform", `translate(${margin.right}, ${height})`);

    let yAxisGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGroup")
      .attr("transform", `translate(0, 0)`);

    let yAxisGridGroup = chartGroup
      .append("g")
      .attr("class", "yAxisGridGroup")
      .attr("transform", `translate(${margin.right}, 0)`);

    let xAxisGridGroup = chartGroup
      .append("g")
      .attr("class", "xAxisGridGroup")
      .attr("transform", `translate(${margin.right}, ${height})`);

    let yAxisRightGroup = chartGroup
      .append("g")
      .attr("class", "yAxisRightGroup")
      .attr("transform", `translate(${width + margin.right}, 0)`);

    yAxisGrid(yAxisGridGroup);
    xAxisGrid(xAxisGridGroup);
    xAxis(xAxisGroup);
    yAxis(yAxisGroup);
    yAxis(yAxisRightGroup);

    const line = d3
      .line()
      .x((d, i) => xScale(d.year))
      .y((d) => yScale(d.mean));

    const lineGroup = chartGroup
      .append("g")
      .attr("class", "lineGroup")
      .attr("transform", `translate(${margin.right}, 0)`);

    lineGroup
      .append("path")
      .datum(grandTotal)
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("d", line);

    const scatterPlot = chartGroup
      .append("g")
      .attr("class", "scatterPlot")
      .attr("transform", `translate(${margin.right}, 0)`);

    const getEvents = (year) => {
      if (year === 1918) {
        return "World War I and the Spanish Flu";
      }
      if (year === 1944) {
        return "World War II";
      }
      if (year === 1958) {
        return "Great Leap Forward";
      }

      return null;
    };

    scatterPlot
      .selectAll("circle")
      .data(grandTotal)
      .enter()
      .append("circle")
      .attr("opacity", (d) => {
        return getEvents(d.year) || d.year === 1800 || d.year === 2020
          ? "1"
          : "0";
      })
      .on("mouseover", function () {
        d3.select(this).style("opacity", "1");
      })
      .on("mouseout", function (event, d) {
        tooltip.transition().duration(200).style("opacity", 1);

        tooltip
          .html(
            "Avg. " +
              d.mean.toFixed(2) +
              "<span>" +
              "Year " +
              d.year +
              "</span>"
          )
          .style("top", event.pageY - margin.bottom + "px")
          .style("left", event.pageX + margin.right + "px");

        d3.select(this).style(
          "opacity",
          getEvents(d.year) || d.year === 1800 || d.year === 2020 ? "1" : "0"
        );
      })
      .attr("cx", (d, i) => {
        return xScale(d.year);
      })
      .attr("cy", (d) => yScale(d.mean))
      .attr("r", "4");

    let getEventsGroup = chartGroup
      .append("g")
      .attr("class", "getEvents")
      .attr("transform", `translate(${margin.right}, 0)`);

    getEventsGroup
      .selectAll("text")
      .data(important_years)
      .enter()
      .append("text")
      .text((d) =>
        getEvents(d.year) !== null
          ? getEvents(d.year)
          : parseFloat(d.mean.toFixed(2))
      )
      .attr("x", (d) => xScale(d.year))
      .attr("y", (d) =>
        getEvents(d.year) !== null
          ? yScale(d.mean) + margin.top
          : yScale(d.mean) - margin.top / 2
      );

    chartGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("stroke-width", "3")
      .attr("x1", xScale(grandTotal[grandTotal.length - 1].year) + margin.right)
      .attr("y1", yScale(grandTotal[0].mean) + margin.top / 1.5)
      .attr("x2", xScale(grandTotal[grandTotal.length - 1].year) + margin.right)
      .attr(
        "y2",
        yScale(grandTotal[grandTotal.length - 1].mean) + margin.top / 1.5
      );

    let generateTriangle = d3.symbol().type(d3.symbolTriangle).size(80);

    chartGroup
      .append("g")
      .attr("class", "getEvents")
      .attr(
        "transform",
        `translate(${
          xScale(grandTotal[grandTotal.length - 1].year) + margin.right
        }, ${
          yScale(grandTotal[grandTotal.length - 1].mean) + margin.top / 1.5
        })`
      )
      .append("path")
      .attr("d", generateTriangle())
      .attr("fill", "#d8d8d8");

    chartGroup
      .append("text")
      .text(
        `+${Math.round(
          grandTotal[grandTotal.length - 1].mean - grandTotal[0].mean
        )}`
      )
      .attr("x", xScale(2020))
      .attr(
        "y",
        yScale(grandTotal[grandTotal.length - 1].mean - grandTotal[0].mean)
      );

    // Population chart

    margin = { top: 20, right: 30, bottom: 30, left: 50 };
    width = 800;
    height = 170;

    let population = [
      {
        value: 0.9,
        year: 1800,
      },
      {
        value: 1.6,
        year: 1900,
      },
      {
        value: 7.5,
        year: 2017,
      },
    ];

    let populationSvg = d3
      .select("#population")
      .append("svg")
      .attr("viewBox", [
        0,
        0,
        width + 2 * margin.right,
        height + 2 * margin.bottom,
      ]);

    let chartPopulationGroup = populationSvg
      .append("g")
      .attr("class", "chartPopulationGroup")
      .attr("transform", `translate(${0}, ${margin.top}) `);

    const xPopulationScale = d3
      .scaleLinear()
      .domain(d3.extent(population, (d) => parseInt(d.year)))
      .range([0, width - 2 * margin.left]);

    const yPopulationScale = d3
      .scaleSqrt()
      .domain([0, population[population.length - 1].value])
      .range([0, height / 1.7]);

    // const xPopulationAxis = d3
    //   .axisBottom(xPopulationScale)
    //   .ticks(3)
    //   .tickFormat((d) => parseInt(d))
    //   .tickSize(0);

    let linePopulationGroup = chartPopulationGroup
      .append("g")
      .attr("class", "linePopulationGroup")
      .attr("transform", `translate(${0}, ${-margin.top + 3})`);

    linePopulationGroup
      .append("line")
      .attr("stroke", "#d8d8d8")
      .attr("stroke-width", "2")
      .attr("x1", margin.left)
      .attr("y1", yPopulationScale(population[2].value))
      .attr("x2", width)
      .attr("y2", yPopulationScale(population[2].value));

    let yAxisPopulationGroup = chartPopulationGroup
      .append("g")
      .attr("class", "yAxisPopulationGroup")
      .attr("transform", `translate(${margin.left}, ${margin.bottom})`);

    yAxisPopulationGroup
      .selectAll("circle")
      .data(population)
      .enter()
      .append("circle")
      .attr("fill", "#051C2C")
      // .attr("opacity", ".1")
      .attr("cx", (d) => xPopulationScale(parseInt(d.year)))
      .attr("cy", yPopulationScale(2))
      .attr("r", (d) => yPopulationScale(d.value));

    // xPopulationAxis(xAxisPopulationGroup);

    let yearTextGroup = chartPopulationGroup
      .append("g")
      .attr("class", "yearTextGroup")
      .attr("transform", `translate(${margin.left}, ${yPopulationScale(7.5)})`);

    yearTextGroup
      .selectAll("text")
      .data(population)
      .enter()
      .append("text")
      .text((d) => d.year)
      .attr("text-anchor", "middle")
      .attr("class", "text")
      .attr("fill", "#999")
      .attr("x", (d) => xPopulationScale(d.year))
      .attr("y", yPopulationScale(2));

    let populationTextGroup = chartPopulationGroup
      .append("g")
      .attr("class", "populationTextGroup")
      .attr("transform", `translate(${0}, ${yPopulationScale(0.9)})`);

    populationTextGroup
      .selectAll("text")
      .data(population)
      .attr("class", "text")
      .enter()
      .append("text")
      .text((d) => d.value)
      .attr("text-anchor", "middle")
      .attr("fill", "#d8d8d8")
      .attr("x", (d) => xPopulationScale(d.year) + margin.left)
      .attr("y", yPopulationScale(2));
  });

  return (
    <section className="life-expectation">
      <header>
        <h4>Article</h4>
        <h2>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.mckinsey.com/featured-insights/coronavirus-leading-through-the-crisis/charting-the-path-to-the-next-normal/life-expectancy-more-than-doubled-over-the-past-century"
          >
            Life expectancy more than doubled over the past century
          </a>
        </h2>
      </header>

      <div>
        <div id="life_exp" className="life_exp">
          <h4>
            <span>Global life expectancy at birth, </span>
            <span>years</span>
          </h4>
        </div>

        <div id="population" className="population">
          <h4>
            <span>Global population, </span>
            <span>billion</span>
          </h4>
        </div>
      </div>
    </section>
  );
}
